html {
font-size: 100%
}

body {
margin: 0;
padding: 0
}

@media print {
body > *:not(main) {
display: none
}
}

[href="#main-content"] {
position: absolute;
top: 0;
right: 100%
}

[href="#main-content"]:focus {
right: auto
}

.incCard {
position: relative
}

.incCard a {
text-decoration: none
}

.incCard a::after {
content: "";
position: absolute;
left: 0;
top: 0;
right: 0;
bottom: 0
}

.incCard a:focus {
text-decoration: underline
}

button {
background-color: #fff;
border: 1px solid #2d2d2d;
padding: 5px 10px;
color: #2d2d2d;
border-radius: 6px;
cursor: pointer;
display: flex;
flex-direction: row;
justify-content: center;
align-items: center
}

button:hover {
background-color: #e6e6e6
}

.bt_icon img {
height: .8rem
}

.bt_XL {
padding: 5px 10px
}

label {
display: flex;
flex-direction: column;
margin-bottom: 1rem
}

.labelTxt {
font-size: .85rem;
text-transform: uppercase;
font-weight: bold
}

textarea, input, select, option {
background-color: #fafafa;
border: 1px solid #ccc;
padding: 10px
}

input::placeholder {
font-style: italic
}

textarea {
height: 25vh
}

.panel_edit {
height: auto;
position: fixed;
bottom: -1400px
}

.fixedBottom {
bottom: 0;
height: auto
}

.panel_edit {
width: 100%;
overflow-x: hidden;
transition: bottom .4s ease-in-out;
box-shadow: -10px -10px 50px rgba(0, 0, 0, .3);
height: 500px;
bottom: -450px;
justify-content: space-between;
background-color: #d9dbe4
}

.panel_edit_wrap {
margin-left: auto;
margin-right: auto;
width: 100%;
max-width: 1500px
}

.panel_edit__split {
display: flex;
flex-direction: row;
justify-content: space-between
}

.panel_edit_inner {
display: flex;
flex-direction: column;
justify-content: flex-start;
padding: 1rem 2rem
}

.edit_header {
width: 100%;
color: #fff;
margin-bottom: 1rem;
background-color: #000c48;
padding: 1rem 2rem
}

.edit_body {
width: 100%;
display: flex;
flex-direction: column
}

.edit_body input, .edit_body select, .edit_body option {
width: 700px
}

.edit_segments {
width: 300px;
display: flex;
flex-direction: column
}

.edit_segments a {
display: block;
text-decoration: none;
color: #000c48;
padding: .5rem 1rem;
border-bottom: 1px solid #d9dbe4;
background-color: #f2f2f6;
margin-right: 3rem
}

.edit_segments a:hover {
color: #fff;
background-color: #17cc98;
color: #000c48
}

@media (min-width: 600px) {
.edit_body {
flex-direction: row
}
}

textarea {
width: 100%;
max-width: 700px;
height: 200px;
font-size: 15px;
border-radius: 2px
}

.edit_workspace {
width: 100%
}

.editorUp {
top: 0px;
height: 100% !important;
}

.editorDown {
bottom: -450px
}

.smallEditField {
height: 2.3rem
}

label:first-of-type {
margin-top: 0
}

.fieldRow {
display: flex;
flex-direction: row;
justify-content: flex-start;
align-items: center
}

.editList ul {
margin: 0
}

.editList li {
display: flex;
flex-direction: row;
justify-content: space-between;
align-items: center;
gap: 1rem;
width: 100%;
max-width: 700px;
margin: 0;
border-bottom: 1px solid #f2f2f6
}

.editList__text {
flex-grow: 1
}

.dataStoryBlocks {
margin-top: 5rem;
margin-bottom: 500px
}

.dsBlock__layout {
width: 100%
}

.dsBlock__left {
width: 76px;
padding-left: .6rem;
border-left: 4px solid #fff
}

.dsBlock__right {
width: 100%;
max-width: 800px;
margin-right: 2rem
}

.dsBlock__margin {
padding-top: .5rem;
padding-bottom: .5rem;
border-top: 1px solid #fff;
border-bottom: 1px solid #fff
}

.dsBlock {
display: flex;
flex-direction: row;
justify-content: center;
transition: background-color .4s;
background-color: #fff
}

.dsBlock h1 {
font-size: 30px;
margin-bottom: .5rem
}

.dsBlock__handle {
border-left: 4px solid #d9dbe4;
text-align: center
}

.dsBlock__handle button {
margin-bottom: .5rem;
min-width: 30px
}

.revealBlock {
visibility: hidden
}

.dsBlock--wide .dsBlock__content {
max-width: 1200px
}

.dsBlock--wideExtra .dsBlock__content {
max-width: 1800px
}

.dsBlock:hover {
background-color: #f2f2f6
}

.dsBlock:hover .revealBlock {
visibility: visible
}

.dsBlock--active {
background-color: #f2f2f6
}

.dsBlock--active .dsBlock__handle {
visibility: visible;
border-left: 4px solid #17cc98
}

.newContent {
background-color: #f2f2f6;
display: none;
flex-direction: row;
gap: 3rem;
width: 100%;
overflow-x: scroll
}

.newContent li {
list-style: none;
margin-bottom: .3rem;
margin-left: 0
}

.dsBlock__new {
padding: .5rem
}

.block_item {
width: 100%;
display: flex;
flex-direction: column;
padding-left: 2rem;
padding-right: 2rem;
transition: border-bottom .5s ease-in-out, border-top .5s ease-in-out;
border-bottom: 1px solid #fff;
border-top: 1px solid #fff;
cursor: pointer;
overflow-x: scroll
}

.block_item h1, .block_item h2, .block_item h3, .block_item h4, .block_item h5, .block_item h6, .block_item p, .block_item ul, .block_item ol, .block_item .editNote {
width: 100%;
max-width: 800px;
margin-left: auto;
margin-right: auto
}

.block_item .editNote {
visibility: hidden;
font-size: .9rem;
color: #8daac1
}

.block_item:hover .editNote {
visibility: visible
}

.block_item--active {
border-left: 8px solid #2cc387
}

.block_item:last-of-type {
margin-bottom: 60vh
}

.block_new {
display: flex;
flex-direction: row
}

.block_new > * {
width: 100%;
max-width: 800px;
margin-left: auto;
margin-right: auto;
display: flex;
flex-direction: row
}

.newMenu {
margin-top: 1rem;
border-radius: 4px;
background-color: #dce5ef;
display: flex;
flex-direction: row;
width: 100%;
overflow-y: hidden;
height: 0px;
transition: height .4s ease-in-out
}

.newMenu > * {
width: 30%;
padding: 1rem .5rem
}

.newMenu > * li {
list-style: none;
margin-left: 0
}

.newBlockButton {
margin-left: calc(-30px - 2rem);
margin-right: 2rem;
width: 30px;
height: 30px;
border: none
}

.dsBlock table {
width: 100%;
margin-left: auto;
margin-right: auto
}

.dsBlock table tr:nth-child(odd) {
background: #f2f2f6
}

.dsBlock table thead tr {
text-align: left;
background: #fff
}

.dsBlock table td {
padding: .2rem;
font-size: .9rem
}

