@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,700");

.dsRepository{
display: block;
margin: 18px 30px;
}

.panelButton{
display: inline-block;
width: 90px;
text-align: center;
border-top: 1px solid #999;
border-bottom: 1px solid #999;
border-left:  1px solid #999;
cursor: pointer;
font-size: smaller;
font-weight: bold;
color: #444444;
}

.panelButton:hover{
background-color: #cccccc;
}

.panelButton:last-child{
border-right:  1px solid #999;
}

.dsResultTable {
margin-top:20px;
display: table;
}

.dsResultHeaderRow{
display: table-row;
font-weight: bold;
font-size: smaller;
}

.dsResultRow, .dsActiveResultRow {
display: table-row;
font-size: smaller;
font-weight: bolder;
cursor: pointer;
}

.dsActiveResultRow {
background-color: #8daac1;
}



.dsResultRow:hover{
background-color: #b0bed9;
}

.dsResultCell{
display: table-cell;
padding: 4px 8px 4px 0 ;
min-width: 200px;
border-bottom: 1px solid #999999;
}

nav{
font-family: "Roboto", sans-serif;
font-weight: bold;
font-size: smaller;
margin-right: 200px;
}

.dsBrandName{
font-family: "Roboto", sans-serif;
font-weight: bold;
}

.panelIcon{
display: inline-block;
width: 12px;
height: 12px;
}

.editorWrapper {
display:block;
width:900px;
margin: 20px 0;
}

.headertext{
width: 1200px;
}

.mdNewLine {
white-space: pre-wrap;
}

.rowBtn {
display: inline-block;
margin: 10px 8px 10px 0;
width: 100px;
}

.writeMsg{
display: inline-block;
font-size: smaller;
color: #ffffff !important;
margin-right: 20px;
}

.newBlockOptionBtn{
display: inline-block;
margin-bottom: 8px;
width: 100px;
}

[id^="map"] {
display: block;
width: 100%;
height: 500px !important;
border: 1px solid #999999;
z-index: 0;
}

.editorPanel{
position:absolute;
right: 490px;
top: 110px;
}

.editorPanelBtn {
display: inline-block;
margin-right: 10px;
width: 120px;
}
